









































































































































































































@import './styles/colors';

a {
  color: white;
  text-decoration: none;
}

.oslo__content {
  display: flex;
  flex: 1 1 auto;
  max-width: 100%;
  position: relative;
  padding-left: 300px;
  flex-wrap: wrap;
}

.oslo__logo {
  margin-top: 3rem;
  margin-bottom: 2rem;
  cursor: pointer;
}

.oslo__navigation-drawer {
  display: flex;
  flex-direction: column;
  background-color: $color-purple;

  &__checkbox {
    display: flex;
    flex-direction: row;
    height: 40px;
  }
}

.oslo__navigation-link {
  $p: &;

  background-color: $color-purple;
  transition: background-color 0.3s ease-in-out;
  padding-left: 2rem;
  color: rgba(white, 0.9);

  &--active {
    padding-left: 2rem;
    background-color: $color-blue;

    #{$p}--label {
      color: $color-purple;
      font-weight: 500;
    }
  }

  &:hover {
    background-color: lighten($color-purple, 10%);
  }

  &--label {
    letter-spacing: 0.3px;
    margin-left: 1rem;
  }
}

.oslo__navigation-topbar {
  background-color: white;
  border-bottom: 1px solid $color-grey-300;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  width: 100%;
}

.oslo__topbar {
  font-weight: bold;
  color: $color-purple;
  margin-bottom: 2rem;

  &-text {
    letter-spacing: 0.8px;
    margin-top: 0.3rem;
    margin-left: 1rem;
  }
}

.v-input--selection-controls {
  margin-top: 0.6rem;
  padding-top: 0;
}

.v-select__selection--uppercase {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 0.7px;
}

input[type='checkbox'] {
  border-bottom-color: white !important;
}
